import React from "react";



import "./assets/styles/bootstrap.css";
import "animate.css";
import "./assets/styles/site.css";
import "bootstrap/dist/js/bootstrap";

import Footer from "./components/footer";
import Navigator from "./components/navigation";
import PageRouter from "./components/_pageRouter";
import { BrowserRouter as Router } from "react-router-dom";

document.body.classList.add("h-100");

var rootHtml = document.getElementsByTagName("html")[0];
rootHtml.setAttribute("class", "h-100");

var rootClass = document.getElementById("root");
if (document.getElementById("root")) {
  rootClass.classList.add("container-fluid", "h-100");
}

function App() {
  return (
    <Router>
      <div className="row d-flex align-items-start flex-row h-100">
        <div className="col-lg-12">
          <Navigator />
        </div>
        <div className="col-12 ">
          <PageRouter />
        </div>
        <footer className="col-12 mt-auto">
          <Footer />
        </footer>
      </div>
    </Router>
  );
}

export default App;
