import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import axios from "axios";

export default function Navigator() {
  const [data, setData] = useState([]);

  useEffect(() => {
    axios
      .get("https://dl.dropboxusercontent.com/s/5e4bhnbfq1toama/nav.json")
      .then((res) => {
        setData(res.data);
      });
  }, []);

  return (
    <header className="navbar navbar-expand-lg mt-md-5">
      <NavLink to="/" className="navbar-brand ms-lg-5" href="#">
        Tuan
        <span className="animate__animated animate__fadeInLeft d-inline-block animate__slower">
          (
        </span>
        <span className="animate__animated animate__bounceIn d-inline-block animate__slower">
          robo
        </span>
        <span className="animate__animated animate__fadeInRight d-inline-block animate__slower">
          )
        </span>
      </NavLink>
      <button
        className="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#menu"
        aria-controls="menu"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span>Menu</span>
      </button>
      <div className="collapse navbar-collapse me-lg-5" id="menu">
        <ul className="navbar-nav ms-auto">
          {data.map((item, index) => (
            <li className="nav-item ms-xl-5" key={index}>
              <NavLink to={item.path} className="nav-link text-capitalize">
                {item.page}
              </NavLink>
            </li>
          ))}
        </ul>
      </div>
    </header>
  );
}
