import React, { useEffect, useState } from "react";
import axios from "axios";

import WorksItem from "../components/_worksItem";
import Modal from "../components/_modal";

export default function LatestWorks() {
  const [data, setData ] = useState([]);

  useEffect(()=>{
    axios.get('https://dl.dropboxusercontent.com/s/s9f44kfrlr359r7/works.json')    
    .then(res => {
      setData(res.data)
    })
  }, [])

  return (
    <div className="my-5">
      <h3 className='offset-lg-1 mb-3 mb-lg-5'>Latest Works</h3>
      <div className="row">
        <div className="col-12">
          <div className="row row-cols-1 row-cols-md-5 g-4">
          {data.map((item, index)=>(
            <React.Fragment key={index}>
            <WorksItem imagePath={item.imagePath} projectName={item.project} targetID={`work${item.ID}`}/>
            <Modal ID={`work${item.ID}`} imagePath={item.imagePath} projectName={item.project}/>
            </React.Fragment>
          ))}   
          </div>
        </div>
      </div>
    </div>
  );
}
